<template>
  <div ref="productHighlightContainer" class="product-highlight-container">
    <div class="product-highlight-top-content-container">
      <div class="product-highlight-header-left">
        <p class="product-highlight-big-text">Transform Your Ride</p>
        <p class="product-highlight-small-text">Elevate Your Carâ€™s Aesthetics with Our Premium Coating Solutions!</p>
        <p v-if="shouldHideProductHighlightHeaderRight" class="product-highlight-smallest-text">Our cutting-edge ceramic coatings provide unmatched protection and an incredible gloss that lasts. Offering a robust shield against scratches, chemicals, and UV damage, our coatings ensure your car stays pristine. With superior hydrophobic properties, dirt and grime slide off effortlessly, making maintenance a breeze. Not only do our solutions enhance your vehicle's shine, but they also offer long-lasting protection, giving you peace of mind knowing your investment is safeguarded against the elements.</p>
      </div>
      <div v-if="!shouldHideProductHighlightHeaderRight" class="product-highlight-header-right">
        <p class="product-highlight-smallest-text-on-right">Our cutting-edge ceramic coatings provide unmatched protection and an incredible gloss that lasts. Offering a robust shield against scratches, chemicals, and UV damage, our coatings ensure your car stays pristine. With superior hydrophobic properties, dirt and grime slide off effortlessly, making maintenance a breeze. Not only do our solutions enhance your vehicle's shine, but they also offer long-lasting protection, giving you peace of mind knowing your investment is safeguarded against the elements.</p>
      </div>
    </div>
    <div class="product-highlight-bottom-content-container">
      <button :disabled="index === 0" @click="showPrevCards(true)" class="prev-button"></button>
      <div class="test1" ref="scrollContainer">
        <div class="test2" v-for="(item, index) in array" :key="index">
          <div class="card-top-section">
              <div v-if="item.data.badge === 'Best Seller'" class="card-badge-best-seller"></div>
              <div class="grey-background"></div>
              <div class="product-highlight-image">
                <img :src="item.data.imageUrl" alt="Product Image" />
              </div>
            </div>
            <div class="card-bottom-section">
              <div class="card-bottom-content-container">
                <p class="product-name">{{ item.data.name }}</p>
                <p class="product-description">{{ item.data.description }}</p>
              </div>
              <button class="card-button" @click="showDetailProduct(index)">View Detail</button>
              <div v-if="item.data.showDetail" class="product-highlight-detail">
                <div class="view-detail-close-button" @click="showDetailProduct(index)">Close</div>
                <p class="detail-product-text" v-for="(detail, detailIndex) in item.data.detail" :key="detailIndex">
                  {{ detail }}
                </p>
              </div>
            </div>
        </div>
      </div>
      <button :disabled="isLastCard" @click="showNextCards(true)" class="next-button"></button>
    </div>
  </div>
</template>
<script>

function cardData(name, imageUrl, badge, variant, detail, description) {
  return {
    data: { name: name,
      imageUrl: imageUrl,
      showDetail: false,
      variant: variant,
      description: description,
      detail: detail,
      badge: badge
    }
  }
}
export default {
  name: "ProductHighlightPage",
  data () {
    return {
      array: [
        cardData( "Clear S2", "https://i.imgur.com/LFldnXV.png", null, ["Red", "Blue", "Yellow"], ["Bagus", "Keren", "Mantap"],  "High Solid Clear yang sangat glossy dan tahan lama"),
        cardData( "2K PU Surfacer", "https://i.imgur.com/Bu16psT.png", null, ["Red", "Cyan", "Yellow"], ["Bagus", "Keren", "Mantap", "Kece"],  "Pelapis primer 2 komponen yang cepat kering, keras dan mudah diaplikasikan"),
        cardData( "Clear 705", "https://i.imgur.com/3HgI32S.png", null, ["1L", "5L", "10L"], ["Bagus", "Keren", "Mantap", "Gokil", "Kinclong"],  "Medium Solid Clear yang glossy dan tahan lama"),
        cardData("Zinc Chromate", "https://i.imgur.com/eOZ8hrf.png", null, ["Red", "Blue", "Yellow"], ["Bagus", "Keren", "Mantap", "Gokil", "Kinclong"], "Pelapis primer sintetik dengan hasil yang lebih halus, anti korosi dan solusi terbaik untuk efisiensi maksimal"),
        cardData("PP Primer Surfacer", "https://i.imgur.com/HnhHlMT.png", null, ["Red", "Blue", "Yellow"], ["Bagus", "Keren", "Mantap", "Gokil", "Kinclong"], "Pelapis primer untuk Plastik (PE) yang kuat dan cepat kering, tidak mudah retak dan terkelupas"),
        cardData( "Epoxy Primer Green", "https://i.imgur.com/0KHKpA3.png", "Best Seller", ["Red", "Blue", "Yellow"], ["Bagus", "Keren", "Mantap", "Gokil", "Kinclong"], "Pelapis primer untuk Plat Metal yang kuat dan cepat kering, mudah diaplikasikan dan daya tutup tinggi")
      ],
      shouldHideProductHighlightHeaderRight: false,
      shouldDisableRightArrow: false,
      cardWidth: 260,
      index: 0,
      isLargeScreen: true,
      isScrolling: false
    }
  },
  mounted() {
    if (window.innerWidth <= 960) {
      this.isLargeScreen = false
    }
    window.addEventListener('resize', this.updateVisibleArray);
  },
  computed: {
    isLastCard() {
      return window.innerWidth <= 960 && this.index >= this.array.length - 1 || window.innerWidth > 960 && this.index >= this.array.length - 3
    }
  },
  methods: {
    scrollToLeft() {
      this.$refs.scrollContainer.scrollLeft = 0;
    },
    showNextCards() {
      if (this.isScrolling || this.index >= this.array.length - 1) return;

      this.isScrolling = true;
      this.index++;

      this.$refs.scrollContainer.scrollBy({
        left: this.cardWidth,
        behavior: 'smooth'
      });

      setTimeout(() => {
        this.isScrolling = false;
      }, 300);
    },
    showPrevCards() {
      if (this.isScrolling || this.index <= 0) return;

      this.isScrolling = true;
      this.index--;

      this.$refs.scrollContainer.scrollBy({
        left: -this.cardWidth,
        behavior: 'smooth'
      });
      setTimeout(() => {
        this.isScrolling = false;
      }, 300);
    },
    showDetailProduct(index) {
      this.visibleArray[index].data.showDetail = !this.visibleArray[index].data.showDetail
    },
    updateVisibleArray() {
      if (window.innerWidth <= 960 && this.isLargeScreen) {
        this.isLargeScreen = false
        this.shouldHideProductHighlightHeaderRight = true
        this.scrollToLeft()
        this.index = 0
      } else if (window.innerWidth > 960 && !this.isLargeScreen) {
        this.isLargeScreen = true
        this.shouldHideProductHighlightHeaderRight = false
        this.scrollToLeft()
        this.index = 0
      }
    },
  }
}

</script>
